/*
  ----------------
  Base styles
  ----------------
*/
html body {
  @media #{$pc} {
    padding-top: 144px; // For fixed menu
  }
  @media #{$sp} {
    padding-top: 80px;
  }

  &.top {
    @media #{$pc} {
      padding-top: 0;
    }
    @media #{$sp} {
      padding-top: 0;
    }
  }

  &.page {
    background: url("../img/common/background_gr.jpg");
    @media #{$pc} {
      padding-top: 130px; // For fixed menu
    }
    @media #{$sp} {
      padding-top: 134px;
    }
  }
}
a{
  &:hover{
    opacity: 0.8;
  }
}
.aside-list {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  @include padding(17px 20px 20px 33px);
  background: #FDF8F0;

  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  &__item {
    display: flex;

    &--pickup {
      display: flex;
      border-bottom: 1px dashed #707070;
      padding-bottom: 20px;

      &:not(:first-of-type) {
        padding-top: 20px;
      }
    }
  }

  a.aside-list__item {
    @include hoverlink;
  }

  .pickup__wrap {
    display: flex;
    // border-bottom: 1px dashed #707070;
  }

  &__txt {
    font-size: 14px;
    letter-spacing: 0.02em;
    line-height: 2;

    .small {
      font-size: 12px;
    }

    &--bold {
      font-weight: bold;
      line-height: 1.8;
    }
  }

  &__img {
    width: 70px;
    height: 70px;
    background-position: 50% 0;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
  }

  &__wrap {
    display: flex;
    flex-direction: column;
    width: calc(100% - 70px);
    padding-left: 13px;

    a {
      @include hoverlink;
    }
  }

  &--pickup {
    background: #fff;
    @include padding(17px 0 20px 0);
    @media #{$sp} {
      @include padding(17px 20px 20px 33px);
    }

    a {
      @include hoverlink;
    }
  }
}

.content-read {
  text-align: center;
  margin-top: 80px;
  margin-bottom: 2.6em;
  @media #{$sp} {
    margin-top: 35px;
  }

  &__txt {
    font-weight: bold;
    letter-spacing: 0.03em;
  }
}

.background_gr {
  background: url("../img/common/background_gr.jpg");
}

.background_dark_gr {
  background: url("../img/common/background_dark_gr.jpg");
}

.flex_box {
  display: flex;
  justify-content: space-between;
  @media #{$sp} {
    display: block;
  }

  &.wrap {
    display: flex;
    flex-wrap: wrap;
  }

  &.a-center {
    align-items: center;
  }
}

.col-2-main {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 40px 0;
  @media #{$sp} {
    display: block;
  }
}

.left-container {
  width: 715px;
  @media #{$sp} {
    width: 100%;
  }
}

.page_contact_bg {
  background: url("../img/common/page-contact_bg.jpg") no-repeat;
  -webkit-background-size: 100% 100%;
  background-size: 100% 100%;
  padding-bottom: 70px;
  position: relative;
  color: #fff;
  @media #{$sp} {
    padding-bottom: 40px;
  }

  h3 {
    font-size: 24px;
    font-family: $font-family-min;
    margin-bottom: 20px;
    padding-left: 40px;
    position: relative;
    display: inline-block;
    @media #{$sp} {
      font-size: 18px;
    }

    &.mail_icon {
      &::before {
        position: absolute;
        content: '';
        width: 28px;
        height: 20px;
        background: url(/assets/img/common/mail_icon.png)no-repeat;
        background-size: 28px 20px;
        top: 8px;
        left: 0;
        @media #{$sp} {
          top: 4px;
        }
      }
    }

    &.tel_icon {
      &::before {
        position: absolute;
        content: '';
        width: 21px;
        height: 29px;
        background: url(/assets/img/common/tel_icon.png)no-repeat;
        background-size: 21px 29px;
        top: 4px;
        left: 0;
        @media #{$sp} {
          top: 0;
        }
      }
    }
  }

  &__left {
    text-align: center;
    width: 100%;
    border-right: 1px solid #fff;
    padding: 20px 0;
    @media #{$sp} {
      border-right: 0 solid #fff;
      border-bottom: 1px solid #fff;
    }
  }

  &__right {
    text-align: center;
    width: 100%;
    padding: 20px 0;

    h3 {
      margin-bottom: 10px;
    }

    .tel_num {
      font-size: 41px;
      font-family: $font-family-min;
      line-height: 1;
      @media #{$sp} {
        font-size: 31px;
      }

      span {
        font-size: 22px;
        @media #{$sp} {
          font-size: 17px;
        }
      }
    }
  }

  p {
    margin-top: 20px;
    font-size: 16px;
    @media #{$sp} {
      font-size: 12px;
    }
  }
}
.hidden{
  display: none;
}
