// Breakpoint設定

$sp: "all and (max-width : 768px)";

$pc: "all and (min-width: 769px)";

// Font-family

$font-family: 'Noto Sans JP',"Hiragino Kaku Gothic ProN", "ヒラギノ角ゴ ProN W3", "メイリオ", Meiryo, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
$lato: 'Lato', sans-serif;
$font-family-min: "A1明朝", "A1 Mincho", "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "HG明朝E", "ＭＳ Ｐ明朝", "ＭＳ 明朝", serif;
$garamond: 'AGaramondPro-Italic', sans-serif;
// カラー
$accent-color: #fc9400;
$secondary-color: #EA5504;


// テキストカラー

$font-color: #000;


// コンテナー設定

$container: 960px;

// グリッド設定

$gutter: 20px;
$small-gutter: 10px;
