/*
  ----------------
  パンくずのスタイル設定
  ----------------
*/
body {
  &.top {
    header {
      .header__container {
        .breadcrumb ol {
          display: none;
        }
      }
    }
  }

  &.page {
    header {
      .header__container {
        .breadcrumb {
          background: rgba(0,0,0,.2);
          min-height: auto;
        }
      }
    }
  }
}

.breadcrumb {
  width: 100%;
  padding: 16px 20px;
  position: relative;
  min-height: 56px;
  color: #333;
  font-weight: bold;

  &__list {
    display: flex;
    align-items: flex-start;
  }

  &__item {
    position: relative;

    &.home {
      font-weight: normal;
    }

    &:not(:last-child) {
      padding-right: 18px;

      &::after {
        content: "";
        display: block;
        position: absolute;
        @include size(6px);
        border-right: 1px solid #000;
        border-bottom: 1px solid #000;
        transform: rotate(-45deg);
        margin-top: -2px;
        top: 50%;
        right: 7px;
      }
    }

    &:last-child .breadcrumb__link {
      pointer-events: none;
    }
  }

  &__link {
    font-size: 14px;
    border-bottom: 1px solid transparent;
    transition: border-color 0.2s;
    will-change: border-color;
    @media #{$sp} {
      font-size: 11px;
    }

    &:hover {
      border-color: #000;
    }
  }
}
