/*
  ----------------
  お問い合わせ
  ----------------
*/
.error-form-box {
  margin-bottom: 30px;
  border: 1px solid #ff0000;
  padding: 30px;
  text-align: center;
  color: #ff0000;
  font-size: 18px;
  line-height: 1.8;
  @media #{$sp} {
    font-size: 14px;
  }
}
form{
  margin-bottom: 40px;
}
.contact_form {
  max-width: 960px;
  margin: 0 auto 50px;
  padding: 80px;
  background: #fff;
  @media #{$sp} {
    padding: 0 15px;
  }

  table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0 20px;

    tr {
      @media #{$sp} {

          td,th {
            display: block;
            width: 100%!important;

          }
          th{
            margin-bottom: 10px;
          }
      }

      th {
        width: 165px;
        text-align: left;
        vertical-align: top;
        font-size: 14px;
        font-weight: bold;
        padding-top: 10px;
        @media #{$sp} {
          font-size: 13px;
          width: 30%;
        }

        span {
          color: #ff0000;
        }
      }

      td {
        width: 635px;
        font-size: 14px;
        @media #{$sp} {
          font-size: 13px;
          width: 70%;
        }

        p {
          margin: 10px 0 20px;
          padding: 0 10px 0 0;
          position: relative;
        }
      }
    }
  }
}

.agree_dec {
  font-size: 16px;
  text-align: center;
  line-height: 2;

  &__agree {
    font-size: 16px;
    text-align: center;
    margin-top: 10px;
    margin-bottom: 40px;

    [type=checkbox]:checked+label:before,
    [type=checkbox]:not(:checked)+label:before,
    [type=checkbox]:not(:checked)+label:before,
    margin-left [type=checkbox]:checked+label:before {
      top: 4px;
    }
  }
  @media #{$sp} {
    font-size: 12px;
    padding:0 15px;
    text-align: left;

  }
}
