/*
  ----------------
  ホームページ
  ----------------
*/
.background_gr_yarn {
  background: url(../img/top/background_yarn.jpg)no-repeat;
  background-size: 100% 100%;
  position: relative;
  @media #{$sp} {
    min-height: 1140px;
    background: url(../img/top/background_yarn_sp.jpg)no-repeat;
    background-size: cover;
  }

  &::after {
    content: '';
    background: url(../img/common/background_gr_arrow.jpg)no-repeat;
    background-size: 100% 100%;
    position: absolute;
    bottom: -80px;
    left: 0;
    right: 0;
    margin: auto;
    width: 45%;
    height: 80px;
    z-index: 1;
    @media #{$sp} {
      content: '';
      background: url("../img/top/background_gr_arrow.png") no-repeat;
      background-size: 100% 100%;
      position: absolute;
      bottom: -35px;
      left: 0;
      right: 0;
      margin: auto;
      width: 90%;
      height: 40px;
      z-index: 1;
    }
  }

  .flex_box {
    @media #{$sp} {
      display: block;
    }
  }
}

.yarn-img {
  width: 300px;
  margin-bottom: 70px;
  z-index: 999;
  @media #{$sp} {
    width: 65%;
    margin-bottom: 0;
  }

  &.center {
    margin-top: 100px;
    @media #{$sp} {
      margin-top: -40px;
      margin-right: 0;
      margin-left: auto;
    }
  }

  &.right {
    margin-top: 50px;
    @media #{$sp} {
      margin-top: 30px;
      padding-bottom: 30px;
    }
  }

  p {
    font-size: 20px;
    font-weight: bold;
    margin: 20px 0;
    text-align: center;
    @media #{$sp} {
      font-size: 100%;
    }

    span {
      color: $accent-color;
      font-size: 22px;
      @media #{$sp} {
        font-size: 120%;
      }
    }
  }
}

.background_over_bgtop {
  background: url(../img/top/background_over_bgtop.jpg)no-repeat;
  background-size: cover;
  position: relative;
  @media #{$sp} {
    background: url(../img/top/background_over_bgtop_sp.jpg)no-repeat;
    padding: 0 15px;
    background-size: 100% 100%;
  }

  .container {
    padding: 60px 0 70px;
    @media #{$sp} {
      padding: 30px 0 35px;
    }

    p {
      font-size: 18px;
      text-align: center;
      color: #fff;
      line-height: 1.8;

      span {
        font-size: 24px;
        font-weight: bold;
        color: $accent-color;
      }
      @media #{$sp} {
        font-size: 16px;

        span {
          font-size: 18px;
        }
      }
    }

    .flex_box {
      margin: 35px 0 0;
      flex-wrap: wrap;
    }

    .col_2 {
      width: 465px;
      margin-bottom: 30px;
      @media #{$sp} {
        width: 100%;
      }
      p {
        text-align: left;
        font-size: 16px;
        padding: 5px;
      }
    }
  }
}

.background_gr_check {
  background: url("../img/common/background_gr_check.jpg");
  position: relative;

  .container {
    padding-bottom: 70px;
  }

  .flex_box {
    &.zirei{
      display: flex!important;
    }
    @media #{$sp} {
      flex-wrap: wrap;
    }

    .col_3 {
      width: 300px;
      margin-bottom: 25px;
      @media #{$sp} {
        width: 49%;
        margin-bottom: 0;

        &:last-child {
          width: 100%;
          position: relative;
          margin-bottom: 20px;
        }
      }

      ul {
        li {
          padding-left: 20px;
          margin: 5px 0;
          position: relative;
          @media #{$sp} {
            font-size: 11px;
            padding-left: 15px;
          }

          &::before {
            content: '';
            position: absolute;
            top: 5px;
            left: 0;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background: $accent-color;
          }
        }
      }

      .last_li_txt {
        margin-top: 110px;
        text-align: right;
        margin-right: 85px;
        @media #{$sp} {
          width: 100%;
          margin-top: 0;
          margin-right: 0;
          position: absolute;
          bottom: 0;
          right: 0;
          text-align: right;
          font-size: 11px;
        }
      }
    }
  }
}

.top_contact_bg {
  background: url(../img/top/top-contact_bg.jpg)no-repeat;
  background-size: 100% 100%;
  position: relative;
  @media #{$sp} {
    background: url("../img/top/top-contact_bg.jpg") no-repeat;
    background-size: auto 100%;
    background-position: top center;
    position: relative;
  }

  .container {
    padding-bottom: 70px;
  }
}

.background_gr {
  .container {
    padding-bottom: 70px;
    @media #{$sp} {
      padding-bottom: 40px;
    }

    .flex_box {
      margin-bottom: 50px;
      flex-wrap: wrap;
      @media #{$sp} {
        display: block;
      }

      .col_3 {
        width: 300px;
        @media #{$sp} {
          width: 100%;
        }

        p {
          margin: 20px 0;
          @media #{$sp} {
            margin-bottom: 30px;
          }
        }
      }
    }
  }
}

.background_dark_gr {

  .flex_box {
    margin-bottom: 40px;
    @media #{$sp} {
      display: block;
    }

    .col_2 {
      width: 48%;
      @media #{$sp} {
        width: 100%;
      }

      h3 {
        font-size: 24px;
        font-family: $font-family-min;
        margin-bottom: 25px;
        @media #{$sp} {
          font-size: 18px;
          margin: 20px 0;
          padding: 0 15px;
        }
      }

      p {
        font-size: 16px;
        width: 90%;
        line-height: 1.8;
        @media #{$sp} {
          width: 100%;
          font-size: 12px;
          padding:0 15px;
        }
      }
    }
  }

  .container:last-child {
    padding-bottom: 70px;
    @media #{$sp} {
      padding-bottom: 40px;
    }
  }
}
