/*
  ----------------
  カレンダー
  ----------------
*/

.cal_left{
  @media #{$sp} {
    text-align: left!important;
    font-size: 14px!important;
  }

}
.calender_box {
  margin-bottom: 40px;
  @media #{$sp} {
    margin-top: 20px;
  }

  h3 {
    font-size: 18px;
  }

  .col_2 {
    width: 49%;
    @media #{$sp} {
      width: 100%;
    }
  }

  table {
    margin: 20px 0 40px;
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;

    tr {
      &:first-child {
        th {
          background: #cccccc!important;
          border-right: 1px solid #fff;

          &:last-child {
            border-right: 1px solid #ccc;
          }
        }
      }

      th {
        background: #f0f0f0;
        padding: 20px;
        border: 1px solid #cccccc;
        text-align: center;
        vertical-align: middle;
        font-size: 16px;
        @media #{$sp} {
          font-size: 12px;
          padding: 15px;
        }

      }

      td {
        background: #fff;
        padding: 20px;
        border: 1px solid #cccccc;
        text-align: left;
        font-size: 16px;
        @media #{$sp} {
          font-size: 12px;
          padding: 15px;
        }

      }
    }
  }
}
