.archive_box {
  padding-bottom: 40px;
  margin-bottom: 40px;
  border-bottom: 1px solid #666;

  .ar-cat {
    background: rgba(252,148,0,0.2);
    font-size: 14px;
    font-weight: bold;
    color: #fc9400;
    padding: 10px 20px;
    display: inline-block;
  }

  h2 {
    font-size: 24px;
    color: #fc7d00;
    font-weight: bold;
    margin: 20px 0;
    @media #{$sp} {
      font-size: 16px;
    }
  }

  .archive_img {
    width: 300px;
    @media #{$sp} {
      width: 100%;
      margin-bottom: 20px;
    }

    img {
      width: 100%;
    }
  }

  .archive_dec {
    width: 385px;
    position: relative;

    p {
      font-size: 16px;
      color: #333;
      line-height: 1.8;
      @media #{$sp} {
        font-size: 14px;
      }
    }
  }

  .mt30 {
    margin-top: 30px;
  }

  .btn-arrow {
    &.more {
      font-size: 14px;
      padding: 10px 25px 10px 35px;
      min-width: 180px;
      margin: 0;
    }
  }
}

.cat_detail {
  background: url(/assets/img/blog/cat_head_img.jpg)no-repeat;
  background-size: 100%;
  margin-bottom: 40px;
  padding: 40px;
  color: #fff;
  @media #{$sp} {
    background: url(/assets/img/blog/cat_head_img_sp.jpg)no-repeat;
    background-size: auto 100%;
    background-position: center;
    padding: 20px;
    margin-bottom: 20px;
  }

  h1 {
    font-size: 24px;
    padding-bottom: 15px;
    border-bottom: 2px solid #fff;
    font-weight: bold;
    @media #{$sp} {
      font-size: 18px;
    }
  }

  p {
    font-size: 16px;
    margin-top: 15px;
    line-height: 1.8;
    @media #{$sp} {
      font-size: 12px;
    }
  }
}
