/*
  ----------------
  サービス紹介
  ----------------
*/
.top_img {
  &.service {
    height: 400px;
    @media #{$sp} {
      height: auto;
    }
    .top_img__headline {
      padding: 40px 0 150px!important;
    }
    .dec_h {
      margin-top: 1px !important;
    }
  }



  p {
    font-size: 20px;
    @media #{$sp} {
      font-size: 18px;
      margin-top: 20px;
    }
  }
}

.service-icon {
  width: 300px;
  text-align: center;
  margin-bottom: 40px;
  font-size: 18px;
  @media #{$sp} {
    width: 48%;
  }

  img {
    width: 100%;
    margin-bottom: 20px;
  }
}

.service_txt_box {
  margin-bottom: 0!important;
  padding-bottom: 80px!important;
  @media #{$sp} {
    padding-bottom: 40px!important;
  }

  .ore {
    color: #fc7d00;
    font-size: 27px!important;
    margin-bottom: 0px!important;
    @media #{$sp} {
      font-size: 19px!important;
      margin-bottom: 15px!important;
    }
  }

  img {
    @media #{$sp} {
      padding: 0 15px;
    }
  }

  .wh_box {
    padding: 15px 30px;
    background: #fff;
    width: 90%;
    margin-top: 20px;
    @media #{$sp} {
      width: auto!important;
      margin-left: 15px;
      margin-right: 15px;
      padding: 15px;
    }

    ul {
      margin: 10px 0;

      li {
        padding-left: 15px;
        position: relative;
        font-size: 15px;
        margin: 5px 0;
        @media #{$sp} {
          font-size: 12px;
        }

        &::before {
          content: '';
          width: 10px;
          height: 10px;
          position: absolute;
          background: #000;
          border-radius: 50%;
          top: 6px;
          left: 0;
        }
      }
    }

    .li_dec {
      font-size: 15px!important;
      color: #333;
      width: 100%!important;
    }
  }

  .left_txt {
    padding-left: 80px;
    @media #{$sp} {
      padding-left: 0;
    }

    .wh_box p {
      width: 100%!important;
    }
  }
}
