/*
  ----------------
  Text
  ----------------
*/
input[type="text"],
input[type="email"],
input[type="tel"] {
  width: 100%;
  padding: 10px;
  font-size: 14px;
  border: 1px solid #ccc;

  &.in_text {
    width: 300px;
    margin-left: 20px;
    position: absolute;
    top: -10px;
    @media #{$sp} {
      position: relative;
      margin: 0;
      top: 0;
      width: 100%;
      border: 1px solid #ccc;
      margin: 15px 0 0;
    }
  }
}

textarea {
  width: 100%;
  border: 1px solid #ccc;
}

input[type="checkbox"] {
  margin-right: 10px;
}

[type="checkbox"]:checked,
[type="checkbox"]:not(:checked) {
  position: absolute;
  left: -9999px;
}

[type="checkbox"]:checked + label,
[type="checkbox"]:not(:checked) + label {
  position: relative;
  padding-left: 1.95em;
  cursor: pointer;
  display: inline-block;
}
/* checkbox aspect */
[type="checkbox"]:checked + label:before,
[type="checkbox"]:not(:checked) + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 2px;
  width: 15px;
  height: 15px;
  border: 2px solid #ccc;
  background: #fff;
}
/* checked mark aspect */
[type="checkbox"]:checked + label:after,
[type="checkbox"]:not(:checked) + label:after {
  content: '\2713\0020';
  position: absolute;
  top: 0.25em;
  left: 0.22em;
  font-size: 1.3em;
  line-height: 0.8;
  color: #0baab4;
  transition: all 0.2s;
  font-family: 'Lucida Sans Unicode', 'Arial Unicode MS', Arial;
}
/* checked mark aspect changes */
[type="checkbox"]:not(:checked) + label:after {
  opacity: 0;
  transform: scale(0);
}

[type="checkbox"]:checked + label:after {
  opacity: 1;
  transform: scale(1);
}

[type="checkbox"]:disabled:checked + label:after {
  color: #999;
}

[type="checkbox"]:disabled + label {
  color: #aaa;
}
