/*
  ----------------
  ヘッダーのスタイル設定
  ----------------
*/
.page {
  .header {
    color: #000;
    background: url("../img/common/background_gr.jpg");
    @media #{$sp} {
      height: auto;
    }

    &.scroll {
      height: auto;
      @media #{$sp} {
        .breadcrumb {
          display: none;
        }
      }
    }

    &__upper {
      @media #{$sp} {
        width: 100%;
        display: block;
        position: relative;
        top: 0;
        left: 0;
        background: transparent;
        z-index: 12;
        padding: 15px 12px;
        color: #000;
      }
    }
  }
}

.open {
  &.page .header__upper {
    color: #fff;
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 130px;
  max-width: none;
  z-index: 9999;
  color: #fff;

  &.scroll {
    height: auto;
    background: url("../img/common/background_gr.jpg");
    box-shadow: 0 3px 3px -3px rgba(0,0,0,0.6);
    color: #000;
    box-shadow: 0 3px 3px -3px rgba(0,0,0,0.6);
    @media #{$sp} {
      height: 80px;
    }
  }

  &__container {
    @include margin(null auto);
  }

  &__wrap {
    display: flex;
    justify-content: space-between;
    @media #{$sp} {
      flex-direction: column;
    }
  }

  &__upper {
    display: flex;
    @media #{$sp} {
      width: 100%;
      display: block;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 12;
      padding: 15px 12px;
    }
  }

  &__logo {
    display: inline-block;
    font-family: $font-family-min;
    font-size: 26px;
    padding: 20px 20px 0;
    z-index: 999;
    @media #{$sp} {
      font-size: 160%;
      @include margin(0 auto null);
      padding: 0;
    }
  }

  &__main {
    padding: 25px 0 0 25px;
  }

  &__desc {
    display: inline-block;
    font-size: 11px;
    margin-top: 30px;
    @media #{$sp} {
      display: block;
      font-size: 10px;
      line-height: 1.77;
      margin: 0;
    }
  }
}

.header-desc {
  font-size: 10px;
  letter-spacing: 0.03em;
  line-height: 1.77;
  @include padding(null 25px null);
}

.ham {
  @media #{$pc} {
    display: none;
  }
  background: #000;
  font-size: 12px;
  text-align: center;
  @include size(58px);
  padding-top: 30px;
  color: #fff;
  position: relative;

  &::after,
  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 50%;
    margin-left: -15px;
    @include size(30px 3px);
    background: #fff;
    transition: transform 0.4s;
    will-change: transform;
  }

  &::before {
    top: 16px;
  }

  &::after {
    top: 27px;
  }
  // Active
  span:nth-child(2) {
    display: none;
  }

  &.is-active {
    span:nth-child(2) {
      display: block;
    }

    span:nth-child(1) {
      display: none;
    }

    &::before {
      transform: rotate(45deg) translate(3px, 3px);
    }

    &::after {
      transform: rotate(-45deg) translate(4px, -5px);
    }
  }
  //
}

.nav {
  @media #{$sp} {
    position: fixed;
    top: 58px;
    bottom: 0;
    left: 0;
    width: 100%;
    background: #fff;
    @include padding(30px 15px null);
    z-index: 12;
    display: none;
    overflow: scroll;
  }

  &__list {
    display: flex;
    @include margin(null -14px);
    @media #{$sp} {
      flex-direction: column;
      @include margin(null 0);
    }
  }

  &__item {
    @media #{$sp} {
      &:not(:last-child) {
        border-bottom: 1px dotted #707070;
      }
    }
  }

  &__link {
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: bold;
    line-height: 2;
    letter-spacing: 0.04em;
    text-align: center;
    @include padding(null 14px);
    position: relative;
    @media #{$sp} {
      width: 100%;
      height: auto;
      font-size: 16px;
      font-weight: bold;
      letter-spacing: 0.03em;
      @include padding(10px 15px);
    }
  }
}

.cta-banner {
  // ie11 fix
  width: 273px;
  //
  @media #{$sp} {
    width: 100%;
  }

  &__body {
    background: #fff;
    color: #fc9400;
    position: relative;
    @include padding(8px 15px);
    @media #{$sp} {
      @include padding(20px 30px);
    }
  }

  &__ttl {
    font-size: 20px;
    font-weight: bold;
    text-align: right;
    @media #{$sp} {
      display: inline-block;
    }

    &:before {
      content: '';
      position: absolute;
      display: block;
      width: 28px;
      height: 37px;
      margin: auto;
      top: 0;
      bottom: 0;
      left: 20px;
      background: url("../img/common/icon_phone_yell.png") no-repeat;
      background-size: 28px 37px;
    }
  }

  &__list {
    display: flex;
    @include margin(null -10px);
  }

  &__item {
    flex: 0 1 50%;
    @include padding(null 10px);
  }

  &__subttl {
    font-size: 12px;
    @media #{$sp} {
      font-size: 12px;
      margin-bottom: 0.2em;
    }
  }

  &__txt {
    font-family: $lato;
    font-size: 18px;
    font-weight: bold;
    letter-spacing: 0.04em;
    padding-left: 25px;
    position: relative;
    @media #{$sp} {
      font-size: 16px;
      margin-bottom: 0.2em;
    }

    &::before {
      content: "";
      display: block;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      @include size(20px 12px);
      background: url("../img/common/icon_freedial.svg") 50% / contain no-repeat;
    }
  }

  &__hours {
    font-size: 10px;
    font-weight: bold;
    letter-spacing: 0.02em;
    @media #{$sp} {
      font-size: 12px;
      font-weight: normal;
    }
  }

  &__nav {
    background: $accent-color;
    display: flex;
    @media #{$sp} {
      background: none;
      flex-direction: column;
      @include padding(null 15px);
      @include margin(30px null 40px);
    }
  }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 0 1 50%;
    height: 40px;
    font-size: 14px;
    font-weight: bold;
    color: #fff;
    position: relative;
    transition: opacity 0.2s;

    &:hover {
      opacity: 0.7;
    }
    @media #{$sp} {
      background: $accent-color;
      height: auto;
      min-height: 50px;
      font-size: 18px;
      letter-spacing: 0.03em;

      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
    @media #{$pc} {
      &:not(:last-child) {
        &::after {
          content: "";
          display: block;
          position: absolute;
          top: 10px;
          bottom: 10px;
          width: 1px;
          right: 0;
          background: #fff;
        }
      }
    }
  }

  &__bottom {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 273px;

    a {
      text-align: center;
      padding: 14.5px 15px 14.5px 25px;
      background: $accent-color;
      color: #fff;
      font-size: 18px;
      font-weight: bold;
      position: relative;
      display: block;
      width: 100%;

      &:before {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        left: 10px;
        width: 0;
        height: 0;
        border: 7px solid transparent;
        border-left: 8px solid #fff;
      }
    }
  }

  &__bottom_sp {
    position: relative;

    a {
      text-align: center;
      padding: 14.5px 15px 14.5px 25px;
      background: $accent-color;
      color: #fff;
      font-size: 18px;
      font-weight: bold;
      position: relative;
      display: block;
      width: 100%;

      &:before {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        left: 10px;
        width: 0;
        height: 0;
        border: 7px solid transparent;
        border-left: 8px solid #fff;
      }
    }
  }
}
// PC after scroll
@media #{$pc} {
  html.is-scrolled {
    padding-top: 144px;

    .header {
      transition: all 0.2s;
    }

    .header__logo {
      .pc-only {
        display: none !important;
      }

      .sp-only {
        display: block !important;
      }
    }

    .header__desc {
      display: none;
    }

    .cta-banner__body {
      display: none;
    }

    .cta-banner__link {
      height: 85px;
    }

    .cta-banner__link:not(:last-child):after {
      top: 32px;
      bottom: 32px;
    }

    .amamori-mega {
      top: 85px;
    }
  }
}

#nav-toggle {
  position: fixed;
  top: 25px;
  right: 25px;
  height: 32px;
  cursor: pointer;

  > div {
    position: relative;
    width: 30px;
  }

  span {
    width: 100%;
    height: 2px;
    left: 0;
    display: block;
    background: #000;
    position: absolute;
    transition: transform 0.6s ease-in-out, top 0.5s ease;

    &:nth-child(1) {
      top: 0;
    }

    &:nth-child(2) {
      top: 10px;
    }

    &:nth-child(3) {
      top: 20px;
    }
  }

  .m_txt {
    height: 0;
    font-size: 60%;
    bottom: -30px;
    text-align: center;

    &::before {
      height: 0;
      bottom: 0;
      content: 'MENU';
    }
  }
}

.top {
  #nav-toggle {
    span {
      background: #fff;
    }
  }
}

.menuwh {
  color: #000!important;

  span {
    width: 100%;
    height: 2px;
    left: 0;
    display: block;
    background: #000!important;
    position: absolute;
  }
}

.open {
  #nav-toggle span {
    background: #fff;

    &:nth-child(1) {
      top: 15px;
      transform: rotate(45deg);
    }

    &:nth-child(2) {
      top: 15px;
      width: 0;
      left: 50%;
    }

    &:nth-child(3) {
      top: 15px;
      transform: rotate(-45deg);
    }

    &.m_txt {
      height: 0;

      &::before {
        height: 0;
        bottom: -30px;
        content: 'CLOSE';
      }
    }
  }
}
/* z-index */
#nav-toggle {
  z-index: 1000;
}

#container {
  z-index: 900;
}

#gloval-nav {
  background: rgba(51,51,51,0.9);
  color: #fff;
  padding: 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -999;
  text-align: center;
  display: flex;
  visibility: hidden;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.6s ease, visibility 0.6s ease;
  overflow: scroll;
}

#gloval-nav {
  a {
    display: block;
    color: #fff;
    text-decoration: none;
    padding: 10px 0;
    transition: color 0.6s ease;

    &:hover {
      color: #666;
    }
  }

  nav {
    width: 100%;
    padding: 0 15px;
  }

  ul {
    list-style: none;

    li {
      opacity: 0;
      transform: translateX(200px);
      transition: transform 0.6s ease, opacity 0.2s ease;

      &:nth-child(2) {
        transition-delay: 0.15s;
      }

      &:nth-child(3) {
        transition-delay: 0.3s;
      }

      &:nth-child(4) {
        transition-delay: 0.45s;
      }

      &:nth-child(5) {
        transition-delay: 0.6s;
      }

      &:nth-child(6) {
        transition-delay: 0.75s;
      }

      &:nth-child(7) {
        transition-delay: 0.9s;
      }
    }
  }
}
/* open */
.open {
  overflow: hidden;

  #gloval-nav {
    visibility: visible;
    opacity: 1;
  }

  #gloval-nav li {
    opacity: 1;
    transform: translateX(0);
    transition: transform 1s ease, opacity 0.9s ease;
    font-size: 16px;
  }
}
