/*
  ----------------
  サイドバーのスタイル設定
  ----------------
*/
aside {
  width: 215px;
  @media #{$sp} {
    width: 100%;
    padding: 0;
    margin: 0;
  }
  a{
    width: 100%;
  }

  .side-blog-bg {
    background: url(/assets/img/sidebar/side_blog_img.jpg)no-repeat;
    background-size: cover;
    width: 100%;
    text-align: center;
    padding: 20px 15px;
    margin-bottom: 30px;
    font-family: $font-family-min;
    color: #fff;
    @media #{$sp} {
      width: 100%;
    }

    p {
      font-size: 16px;
      font-weight: bold;
      line-height: 1.8;
    }

    h3 {
      font-size: 27px;
      font-weight: bold;
      margin-top: 15px;
    }
  }

  h4 {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 20px;
  }

  .cat_li {
    list-style-type: none;

    li {
      a {
        padding-left: 15px;
        color: #fc9400;
        margin-bottom: 5px;
        position: relative;
        font-size: 16px;

        @media #{$sp} {
          margin-bottom: 15px;
        }

        &::before {
          position: absolute;
          content: '';
          width: 10px;
          height: 10px;
          border-radius: 50%;
          background: #fc9400;
          top: 8px;
          left: 0;
        }
      }
    }
  }
}
