/*
  ----------------
  フッタースタイル設定
  ----------------
*/
.footer {
  position: relative;
  background: url("../img/common/foot_bg.jpg");
  background-size: cover;
  padding: 60px 0 50px;
  color: #fff;
  @media #{$sp} {
    @include padding(60px 15px 40px);
  }

  &__main {
    width: $container;
    margin: 0 auto;

    .foot_flex {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      @media #{$sp} {
        display: block;
      }

      .foot_c_left {
        width: 43%;
        @media #{$sp} {
          width: 100%;
        }

        h2 {
          font-size: 26px;
          font-weight: bold;
          font-family: $font-family-min;
          @media #{$sp} {
            font-size: 22px;
          }
        }

        h3 {
          font-size: 16px;
          font-weight: bold;
          margin: 15px 0;
          @media #{$sp} {
            font-size: 14px;
          }
        }

        p {
          font-size: 14px;
          margin: 15px 0;
          @media #{$sp} {
            font-size: 12px;
          }
        }
      }

      .foot_c_right {
        width: 48%;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        @media #{$sp} {
          width: 100%;
        }

        .foot_menu {
          width: 50%;

          ul {
            list-style-type: none;

            li {
              margin: 10px 0;
              @media #{$sp} {
                margin: 15px 0;
              }
              a {
                font-size: 15px;
                color: #fff;
                @media #{$sp} {
                  font-size: 12px;
                }
              }
            }
          }
        }

        &__copy {
          width: 100%;
          font-size: 12px;
          letter-spacing: 0.04em;
          text-align: right;
          @media #{$sp} {
            text-align: center;
            font-size: 11px;
          }
        }
      }
    }
    @media #{$sp} {
      width: 100%;
    }
  }
}
