/*
  ----------------
  ページネーション
  ----------------
*/
.pager {
  margin-bottom: 40px;
}

.pager ul.pagination {
  text-align: center;
  margin: 0;
  padding: 0;
}

.pager .pagination li {
  display: inline;
  margin: 0 2px;
  padding: 0;
  display: inline-block;
  background: #fff;
  width: 50px;
  height: 50px;
  text-align: center;
  position: relative;
}

.pager .pagination li a {
  vertical-align: middle;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  display: table;
  color: #000;
  text-decoration: none;
  border: 1px solid #e5e5e5;
}
.pager .pagination li.more{
  background: transparent;
}
.pager .pagination li.more span {
  position: absolute;
  margin: auto 0;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
}

.pager .pagination li a span {
  display: table-cell;
  vertical-align: middle;
  position: relative;
}

.pager .pagination li a.active,
.pager .pagination li a:hover {
  color: #fff;
  background: #fc9400;
  border: 1px solid #fc9400;
}

.pager .pagination li.pre {
  margin-right: 15px;
}

.pager .pagination li.next {
  margin-left: 15px;
}

.pager .pagination li.pre span::before {
  content: "";
  display: block;
  position: relative;
  height: 10px;
  width: 10px;
  border-left: 1px solid #000;
  border-top: 1px solid #000;
  transform: rotate(-45deg);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  @media #{$sp} {
    margin-top: -5px;
    right: 5px;
  }
}

.pager .pagination li.next span::before {
  content: "";
  display: block;
  position: relative;
  height: 10px;
  width: 10px;
  border-right: 1px solid #000;
  border-bottom: 1px solid #000;
  transform: rotate(-45deg);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  @media #{$sp} {
    margin-top: -5px;
    left: 5px;
  }
}

.pager .pagination li.pre a:hover span::before {
  border-left: 1px solid #fff;
  border-top: 1px solid #fff;
}

.pager .pagination li.next a:hover span::before {
  border-right: 1px solid #fff;
  border-bottom: 1px solid #fff;
}
@media #{$sp} {
  .pager .pagination li {
    width: 30px;
    height: 30px;
    margin: 0;
  }

  .pager .pagination li.next a,
  .pager .pagination li.pre a {
    width: 100%;
    text-align: center;
  }
}
