/*
  ----------------
  Headings
  ----------------
*/
.top {
  .top_img {
    color: #fff;
    width: 100%;
    padding: 0;
    position: relative;

    img {
      object-fit: cover;
      object-position: top left;
      width: 100%;
      height: 100%;
      z-index: -999;
      position: absolute;
      @media #{$sp} {}
    }

    &__headline {
      padding: 130px 90px 70px;
      @media #{$sp} {
        padding: 200px 15px 150px;
      }
    }

    .dec {
      font-family: $garamond;
      font-style: italic;
      font-size: 32px;
      line-height: 1.5;
      @media #{$sp} {
        font-size: 16px;
        text-align: center;
      }
    }

    h1 {
      font-family: $font-family-min;
      font-size: 72px;
      line-height: 1.3;
      margin: 20px 0;
      @media #{$sp} {
        font-size: 33px;
        padding-left: 0;
        text-indent: 0;
        text-align: center;
      }
    }

    p {
      font-family: $font-family-min;
      font-size: 22px;
      line-height: 2;
      @media #{$sp} {
        font-size: 15px;
        text-align: center;
      }
    }

    &__overlay {
      bottom: 0;
      width: 100%;
      background: rgba(0,0,0, 0.4);
      padding: 25px 0;
      text-align: center;

      p {
        font-size: 20px;
        font-family: $font-family-min;
        line-height: 1;
        margin-top: 10px;
        @media #{$sp} {
          font-size: 12px;
          margin-top: 20px;
        }
      }
    }
  }
}

.page {
  .top_img {
    color: #fff;
    width: 100%;
    height: 285px;
    padding: 0;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      object-fit: cover;
      object-position: top center;
      width: 100%;
      height: 100%;
      z-index: -999;
      position: absolute;
      left: 0;
      top: 0;
      @media #{$sp} {}
    }

    &__headline {
      position: relative;
      text-align: center;
      @media #{$sp} {
        padding: 200px 15px 150px;
      }
    }

    .dec_ore {
      border: 2px solid #fc9400;
      border-radius: 50px;
      color: #fc9400;
      text-align: center;
      display: inline-block;
      padding: 10px 15px;
      margin-bottom: 20px;
      font-weight: bold;
      font-family: $font-family-min;
      @media #{$sp} {
        font-size: 15px;
      }
    }

    .dec_h {
      font-size: 37px;
      font-family: $font-family-min;
      @media #{$sp} {
        font-size: 22px;
      }
    }

    h1 {
      font-family: $font-family-min;
      font-size: 42px;
      line-height: 1.3;
      @media #{$sp} {
        font-size: 30px;
        padding-left: 0;
        text-indent: 0;
        text-align: center;
      }
    }

    &__overlay {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      background: rgba(0,0,0, 0.4);
      padding: 25px 0;
      text-align: center;

      p {
        font-size: 16px;
        font-family: $font-family-min;
        line-height: 1;
        margin-top: 10px;
        @media #{$sp} {
          font-size: 12px;
          margin-top: 20px;
        }
      }
    }
  }
}

.heading-primary {
  padding-top: 75px;
  padding-bottom: 40px;

  p {
    font-size: 18px;
    text-align: center;
    margin-top: 40px;
    line-height: 1.8;
  }
  @media #{$sp} {
    padding-top: 20px;
    padding-bottom: 20px;

    p {
      font-size: 13px;
    }
  }

  &__ttl {
    font-size: 35px;
    font-weight: bold;
    text-align: center;
    font-family: $font-family-min;
    color: #333333;
    @media #{$sp} {
      font-size: 19px;
      padding-top: 1em;
    }

    &.white {
      color: #fff!important;
    }

    &__dec {
      text-align: left!important;

      span {
        color: #ff0000;
      }
    }
  }
}
