/*
  ----------------
  強み
  ----------------
*/
.top_img {
  &.strengths {
    height: auto;
    @media #{$sp} {
      height: auto;
    }

    .top_img__headline {
      padding: 80px 0;
    }
  }

  p {
    font-size: 20px;
    margin-top: 10px;
    @media #{$sp} {
      font-size: 18px;
      margin-top: 20px;
    }
  }
}

.strengths_num {
  margin-bottom: 80px;

  h2 {
    margin-top: 100px;
    margin-bottom: 60px;
    font-size: 35px;
    font-family: $font-family-min;
    padding-left: 135px;
    position: relative;
    @media #{$sp} {
      margin-top: 60px;
      margin-bottom: 40px;
      font-size: 21px;
      padding-left: 80px;
    }

    span {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      width: 110px;
      height: 110px;
      border-radius: 50%;
      background: #fc7d00;
      font-size: 28px;
      text-align: center;
      margin: auto;
      color: #fff;
      padding: 10px;
      @media #{$sp} {
        width: 70px;
        height: 70px;
        font-size: 17px;
      }

      span {
        font-size: 48px;
        background: transparent;
        margin-top: 30px;
        @media #{$sp} {
          font-size: 28px;
          margin-top: 15px;
        }
      }
    }
  }

  &__box {
    .col_2 {
      width: 49%;
      @media #{$sp} {
        width: 100%;
      }

      p {
        @media #{$sp} {
          margin: 20px 0;
        }
      }
    }
  }

  .left_txt {
    padding-left: 0;
  }
}

.stre_btn {
  margin: 30px 0;
}

.strengths_pic__box {
  .col_2 {
    @media #{$sp} {
      margin-top: 25px;
    }
  }

  h3 {
    font-size: 22px;
    margin: 25px 0;
    @media #{$sp} {
      padding: 0!important;
    }
  }

  p {
    font-size: 16px!important;
    width: 100%!important;
    @media #{$sp} {
      padding: 0!important;
    }
  }
}
