/*
  ----------------
  実績・事例
  ----------------
*/
body.case {
  .top_img {
    &.case {
      height: auto;
      @media #{$sp} {
        height: auto;
      }
  
      .top_img__headline {
        padding: 80px 0;
      }
    }
  
    p {
      font-size: 20px;
      margin-top: 30px;
      @media #{$sp} {
        font-size: 18px;
        margin-top: 20px;
      }
    }
  }
  
  .background_gr_check .flex_box .col_3 .last_li_txt {
    margin-top: 3.5em;
    font-weight: normal !important;
  }
  @media #{$sp} {
    .background_gr_check .flex_box .col_3 .last_li_txt {
      bottom: -2em;
    }
  }
  
  .background_dark_gr.service {
    background:none;
    h3 {
      font-weight: bold;
    }
    p {
      text-indent: -1em;
      padding-left: 1em;
    }
    @media #{$pc} {
      p {
        max-width: 75%;
      }
    }
  }
  .background_dark_gr.about {
    h3 {
      font-weight: bold;
    }
    @media #{$pc} {
      p {
        max-width: 75%;
      }
    }
  }

  .flex_box.zirei {
    font-weight: bold;
  }
}